@font-face {
  font-family: Avenir-Book;
  src: url("Avenir-Book.657c3568.ttf");
}

@font-face {
  font-family: Avenir-Heavy;
  src: url("Avenir-Heavy.01d9ab74.ttf");
}

body {
  --border-radius: 5px;
  --text-ident: 20px;
  --border: 1px solid #e1e7ed;
  flex-direction: column;
  gap: 20px;
  margin-top: 20px;
  font-weight: 300;
  display: flex;
}

.big-input-cont__big-input, .small-input-cont__small-input {
  text-indent: var(--text-ident);
  border-radius: var(--border-radius);
  border: var(--border);
  font-family: Avenir-Book, serif;
}

.big-input-cont__big-input {
  box-sizing: border-box;
  width: 100%;
  height: 70px;
  padding-left: 42px;
  font-size: 16px;
}

.small-input-cont__small-input {
  width: 98.3%;
  height: 38px;
  padding-left: 13px;
  font-size: 14px;
}

.big-input-cont__big-input:hover {
  box-shadow: 1px 4px 4px #00000040;
}

.small-input-cont__small-input:hover {
  box-shadow: 1px 2px 4px #00000040;
}

.big-input-cont__big-input:focus, .small-input-cont__small-input:focus {
  background: linear-gradient(#fff, #f6f6f7 100%);
  outline: none;
  font-family: Avenir-Heavy, serif;
}

.big-input-cont__big-input::placeholder, .small-input-cont__small-input::placeholder {
  text-indent: var(--text-ident);
  color: #3d4e61;
}

.big-input-cont:before {
  content: "";
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  width: 19px;
  height: 19px;
  position: absolute;
  top: 45px;
  left: 34px;
}

.small-input-cont:before {
  content: "";
  background-image: url("Search copy.6eff4205.svg");
  background-repeat: no-repeat;
  width: 11px;
  height: 11px;
  position: absolute;
  top: 125px;
  left: 21px;
}
